@import url('https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@800&family=Roboto+Mono&display=swap');

.aboutpage {
    height: 100vh;
    background-image: linear-gradient(rgb(0, 0, 0), rgb(4, 4, 57), rgb(12, 15, 160));
}

.aboutme {

    padding-left: 15em;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 100vw;
    height: 100vh;


}

.burg {
    position: absolute;
    height: 35px;
    width: 35px;
}


.whoami {
    margin-left: 2em;
    width: 50%;
    text-align: justify;
    flex-basis: auto;
    color: rgb(21, 187, 187)
}

.whoami p {
    font-family: 'Roboto Mono', monospace;
    width: 100%;
    font-size: 2em
}

.whoami h1 {
    font-size: 4em;
    width: auto;
    font-family: 'Merriweather Sans', sans-serif;
    overflow: hidden;
    border-right: .15em solid orange;
    white-space: nowrap;
    margin: 0 auto;
    letter-spacing: .15em;
    animation:
        typing 3.5s steps(25, end),
        blink-caret .75s step-end infinite;

}

@keyframes typing {
    from {
        width: 0
    }

    to {
        width: 100%
    }
}

@keyframes blink-caret {

    from,
    to {
        border-color: transparent
    }

    50% {
        border-color: orange;
    }
}



.logo1 {
    grid-area: logo1;
    background-image: url('../../static/img/javascript.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

