.logos {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-template-areas:
        "logo1 logo3"
        "logo2 logo8"
        "logo5 logo6"
        "logo7 logo4";
    border-right: solid 2px #b72a2a;
    border-top: solid 2px #b72a2a;
    border-bottom: solid 2px #b72a2a;
    margin-bottom: 1em;
    margin-right: 1em;
    margin-top: 1em;
    box-shadow: 10px 10px 10px #b72a2a;
}

.myskills {

    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: 1fr;
    grid-template-areas: ". skillstext";
    height: 100vh;


}

.skillstext {
    grid-area: skillstext;
    margin-right: 20px;
    align-self: center;
    justify-self: center;
    text-align: center;

}

.skillstext h1 {
    font-size: 5em;
    width: auto;
    font-family: 'Merriweather Sans', sans-serif;
    color: rgb(199, 174, 6);


}

.skillstext p {
    color: rgb(165, 187, 21);
    font-family: 'Roboto Mono', monospace;
    width: 100%;
    font-size: 1.75em;
    line-height: 3em;
    padding-right: 2em;


}

/*===========================MOBILE========================*/

@media only screen and (max-width: 850px) {
    .aboutme {

        padding-left: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100vw;
        height: 100vh;


    }

    .whoami {
        margin-left: 0;
        width: 90%;
        text-align: none;
        flex-basis: none;
        color: rgb(21, 187, 187)
    }

    .whoami p {
        font-family: 'Roboto Mono', monospace;
        width: 100%;
        font-size: .75em
    }

    .whoami h1 {
        font-size: 1.4em;
        margin-top: 1em;
    }



    .logos {
        display: none;
    }


    .myskills {

        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
        grid-template-areas: " skillstext"
            ".";
        height: 100vh;


    }

    .skillstext {
        grid-area: skillstext;
        margin-right: 20px;
        margin-left: 20px;
        align-self: center;
        justify-self: center;
        text-align: center;

    }

    .skillstext h1 {
        font-size: 3em;
        width: auto;
        font-family: 'Merriweather Sans', sans-serif;
        color: rgb(199, 174, 6);


    }

    .skillstext p {
        color: rgb(165, 187, 21);
        font-family: 'Roboto Mono', monospace;
        width: 100%;
        font-size: 1em;
        line-height: 3em;
        padding-right: 2em;


    }




}