button {


    padding: 0.5em 3em;
    border: 0.2em solid bisque;
    margin: 0 0.3em 0.3em 0;
    box-sizing: border-box;
    text-decoration: none;
    text-align: center;
    transition: all 0.15s;
    font-size: 1em;
    color: bisque;

}

button a {
    text-decoration: none;
    color: inherit;
}

.closebutton {
    z-index: 4;
    width: fit-content;
    height: auto;
    color: red;
    font-size: 2em;
    font-style: bold;
    cursor: pointer;
    align-self: flex-end;
    margin-right: 30px;
}

.greyback {
    top: 0;
    z-index: 2;
    position: absolute;
    width: 100vw;
    height: 100vh;
    background-color: rgb(0, 0, 0, .75);
}

.greybackclosed {
    display: none
}

.overlaydesc {
    z-index: 3;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    top: 50%;
    left: 50%;
    height: 90vh;
    width: 50vw;
    margin-top: -45vh;
    margin-left: -25vw;
    position: absolute;
    border: solid 5px rgb(35, 154, 209);
    color: white;
    background-color: rgb(0, 0, 0, 1);
    font-size: 1.75em;
    padding-left: 5px;
    padding-right: 5px;
}

.overlaydesc p {
    padding-left: 5%;
    padding-right: 5%;
}

.overlaydesc h1 {
    margin: 0;
    line-height: 0
}

.twogallery {
    height: 100vh;
    position: relative;

}

.twogallery h1 {
    text-align: center;
}

.thegallery {

    display: grid;
    justify-items: center;
    align-items: start;
    height: 100vh;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-areas:
        ". . . . .  "
        ". pic1 . pic2 . "
        ". tag1 . tag2 . "
        ". title title title . "
        ". pic3 . pic4 . "
        ". tag3 . tag4 . "
        ". . . . . "
    ;

}

.page-title {
    display: flex;
    align-self: center;
    grid-area: title;
    font-size: 1em;
    margin: 5em;
    filter: drop-shadow(10px 10px 5px rgb(0 0 0 / 0.5));
}

.page-title h1 {
    text-align: center;
    color: white;
    font-size: 8em;
    margin: 0;

}

.app-pic1 {
    background-image: url('../../static/img/routesmall.png');
    background-repeat: no-repeat;
    background-size: contain;
    height: 300px;
    width: 500px;
    grid-area: pic1;
    background-position: center;
    margin: 5px;
    cursor: pointer;
    filter: drop-shadow(2px 2px 5px rgb(0 0 0 / 0.5));
}

.app-pic2 {
    background-image: url('../../static/img/Thriftologysmall.png');
    background-repeat: no-repeat;
    background-size: contain;
    height: 300px;
    width: 500px;
    grid-area: pic2;
    background-position: center;
    margin: 5px;
    cursor: pointer;
    filter: drop-shadow(2px 2px 5px rgb(0 0 0 / 0.5));
}


.app-pic3 {
    background-image: url('../../static/img/gobanans.png');
    background-repeat: no-repeat;
    background-size: contain;
    height: 300px;
    width: 500px;
    grid-area: pic3;
    background-position: center;
    margin: 5px;
    cursor: pointer;
    filter: drop-shadow(2px 2px 5px rgb(0 0 0 / 0.5));

}

.app-pic4 {
    background-image: url('../../static/img/Sadiessmall.png');
    background-repeat: no-repeat;
    background-size: contain;
    height: 300px;
    width: 500px;
    grid-area: pic4;
    background-position: center;
    margin: 5px;
    cursor: pointer;
    filter: drop-shadow(2px 2px 5px rgb(0 0 0 / 0.5));
}

.tag-1 {
    grid-area: tag1;

}

.tag-2 {
    grid-area: tag2;

}

.tag-3 {
    grid-area: tag3;

}

.tag-4 {
    grid-area: tag4;

}

.tag-1 h3,
.tag-2 h3,
.tag-3 h3,
.tag-4 h3 {
    margin: 0px;
    padding: 5px;
    color: white;
    font-size: 1.25em;
}

